<template>
  <div>
    <div class="u-plhead">
      <div
        class="zz"
        :style="'background:url(' + gd.coverImgUrl + ')'"
        style="background-size: 200px 200px; "
      ></div>
      <div class="zhong">
        <div class="left">
          <span>{{ gd.playCount }}</span>
          <p>歌单</p>
          <img :src="gd.coverImgUrl" alt="" />
        </div>
        <div class="right">
          <p>{{ gd.name }}</p>
          <div class="bottom">
            <div class="left2">
              <img :src="gd.creator.avatarUrl" alt="" />
            </div>
            <span>
              {{ gd.creator.nickname }}
            </span>
          </div>
        </div>
      </div>
    </div>
    <div class="lb">
      歌曲列表
    </div>
    <div class="m-sglst">
      <ul>
        <li v-for="(item, index) in Gqxq" :key="index">
          <div class="left">
            {{ index + 1 }}
          </div>
          <div class="right">
            <div class="top2">
              {{ item.name }}
            </div>
            <div class="bottom3">
              <span v-for="(item2, index) in item.ar" :key="index">{{
                item2.name
              }}</span
              >-{{ item.al.name }}
            </div>
          </div>
          <div class="right2"></div>
        </li>
      </ul>
      <div class="gengd"></div>
      <p>更多歌曲，请下载客户端</p>
    </div>
    <div class="m-comments">
      <h4>最新评论</h4>
      <div class="cmt_list">
        <ul>
          <li v-for="(item, index) in pl" :key="index">
            <div class="left">
              <img :src="item.user.avatarUrl" alt="" />
            </div>
            <div class="right">
              <p class="p1">
                {{ item.user.nickname }}
              </p>
              <p class="p2">{{ item.time | formatDate }}</p>
              <p class="p3">{{ item.content }}</p>
              <span>{{ item.likedCount != 0 ? item.likedCount : "" }}</span>
              <img src="@/assets/1609894060(1).png" alt="" />
            </div>
          </li>
        </ul>
      </div>
    </div>
    <div class="footer-wrap">
      <div>打开</div>
      <div class="div2">下载</div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      rg: [], //歌曲列表
      gd: [], //歌单信息头部
      pl: [], //歌单评论
      id: [], //遍历之后的id  歌曲列表id
      idString: [], //转换成字符串的id,
      Gqxq: [], //歌单内容
    };
  },
  mounted() {
    this.getList();
    this.gdlist();
  },
  methods: {
    getList() {
      let id = this.$route.query.id;
      this.$http.post("api/playlist/detail?id=" + id + "").then((res) => {
        this.rg = res.data.playlist.trackIds;
        this.gd = res.data.playlist;
        // console.log(res);
        // console.log(this.gd, "iddddddd");
        // console.log(this.rg, "////////////////");
        this.rg.forEach((item) => {
          if (this.id.length < 10) {
            this.id.push(item.id);
          }
        });
        this.idString = this.id.toString();
        this.$http
          .post("api/song/detail?ids=" + this.idString + "")
          .then((res) => {
            this.Gqxq = res.data.songs;
            console.log(res);
            console.log(this.Gqxq, "gggggggg");
          });
      });
    },
    //歌单评论
    gdlist() {
      let id = this.$route.query.id;
      this.$http.post("api/comment/playlist?id=" + id + "").then((res) => {
        this.pl = res.data.comments;
        // console.log(this.pl);
      });
    },
  },
};
</script>
<style lang="less">
* {
  margin: 0;
  padding: 0;
}
.u-plhead {
  width: 100%;
  height: 186px;
  padding: 30px 10px 30px 15px;
  position: relative;
  z-index: 0;
  overflow: hidden;
  box-sizing: border-box;
  .zz {
    width: 100%;
    height: 186px;
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    opacity: 0.5;
  }
  .zhong {
    width: 100%;
    height: 126px;
    position: absolute;
    z-index: 5;
    .left {
      width: 126px;
      height: 126px;
      float: left;
      position: relative;
      p {
        width: 40px;
        height: 17px;
        background-color: rgba(217, 48, 48, 0.8);
        font-size: 9px;
        text-align: center;
        border-top-right-radius: 7px;
        border-bottom-right-radius: 7px;
        position: absolute;
        left: 0;
        top: 15px;
        color: #fff;
      }
      span {
        position: absolute;
        right: 5px;
        top: 3px;
        color: #fff;
        font-size: 12px;
      }
      img {
        width: 100%;
        height: 100%;
      }
      .img2 {
        width: 14px;
        height: 14px;
        position: absolute;
        right: 42px;
        top: 3px;
      }
    }
    .right {
      width: 208px;
      height: 126px;
      float: left;
      margin-left: 12px;
      p {
        color: #fff;
      }
      .bottom {
        width: 100%;
        height: 30px;
        margin-top: 40px;
        color: hsla(0, 0%, 100%, 0.7);
        .left2 {
          width: 30px;
          height: 30px;
          display: inline-block;
          vertical-align: -7px;
          img {
            width: 100%;
            border-radius: 15px;
          }
        }
      }
    }
  }
}
.lb {
  height: 23px;
  line-height: 23px;
  padding: 0 10px;
  font-size: 12px;
  color: #666;
  background-color: #eeeff0;
}
.m-sglst {
  position: relative;
  ul {
    list-style: none;
    li {
      width: 100%;
      height: 55px;
      position: relative;
      border-bottom: 1px solid rgb(235, 235, 235);
      .left {
        width: 40px;
        height: 55px;
        text-align: center;
        line-height: 55px;
        font-size: 17px;
        color: #999;
        float: left;
      }
      .right {
        width: 289px;
        height: 55px;
        float: left;
        padding: 6px 0;
        box-sizing: border-box;
        overflow: hidden;

        .top2 {
          font-size: 17px;
        }
        .bottom3 {
          font-size: 12px;
          height: 10px;
          color: #888;
          margin-top: 7px;
          font {
            height: 10px;
          }
        }
      }
      .right2 {
        width: 22px;
        height: 22px;
        position: absolute;
        right: 15px;
        top: 50%;
        transform: translateY(-50%);
        background: url(/img/index_icon.5207a28c.jpg);
        background-position: 144px 0;
        background-size: 169px;
      }
    }
  }
  .gengd {
    width: 100%;
    height: 194px;
    line-height: 194px;
    background-color: #e6e4e4;
    opacity: 0.6;
    text-align: center;
    position: absolute;
    bottom: -55px;
    filter: blur(8px);
  }
  p {
    text-align: center;
    position: absolute;
    bottom: -50px;
    left: 50%;
    transform: translateX(-50%);
    color: #999;
  }
}
.m-comments {
  margin-top: 64px;
  h4 {
    padding: 0 10px;
    height: 23px;
    line-height: 23px;
    color: #666;
    font-size: 12px;
    font-weight: 400;
    background: rgba(0, 0, 0, 0.05);
  }
  .cmt_list {
    ul {
      list-style: none;
      li {
        width: 100%;
        height: 87px;
        display: flex;
        padding-top: 10px;
        .left {
          display: inline-block;
          img {
            width: 30px;
            height: 30px;
            margin: 0 10px;
            border-radius: 50%;
          }
        }
        .right {
          flex: 1;
          position: relative;
          .p1 {
            font-size: 14px;
            color: #666;
            line-height: 20px;
          }
          .p2 {
            font-size: 9px;
            color: #999;
          }
          .p3 {
            color: #333;
            font-size: 15px;
            line-height: 22px;
            margin-top: 5px;
          }
          img {
            top: 0;
            position: absolute;
            right: 5px;
            top: 10px;
          }
          span {
            position: absolute;
            right: 32px;
            top: 10px;
            color: #ccc;
          }
        }
      }
    }
  }
}
.footer-wrap {
  width: 100%;
  height: 54px;
  position: fixed;
  bottom: 0;
  padding-top: 7px;
  padding-bottom: 7px;
  padding-left: 5px;
  padding-right: 5px;
  background-color: #fff;
  display: flex;
  box-sizing: border-box;
  div {
    width: 50%;
    height: 40px;
    border: 1px solid red;
    border-radius: 30px;
    text-align: center;
    line-height: 40px;
    float: left;

    &.div2 {
      background-color: red;
      margin-left: 10px;
    }
  }
}
</style>
